import React from "react";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import Price from "../Price";
import { styles } from "../styles";

const useStyles = makeStyles((theme) => ({
    ...styles,
    rootBg: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type PriceStoryProps = {
    mode: string,
    brand: BrandType
    symbol: string
    product: string
    totalPrice: string
    oldPartialPrice: string
    partialPrice: string
    totalPeople: string
    totalDays: string
}

const PriceStory = ({
    mode, brand, symbol, product, totalPrice, oldPartialPrice, partialPrice, totalPeople, totalDays
}: PriceStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="Price">
                <Typography variant="h3">Price</Typography>
                <br />
                <Divider />
                <br />
                <Grid item xs={2}>
                    <Price
                        symbol={symbol}
                        classes={classes}
                        product={product}
                        totalPrice={totalPrice}
                        oldPartialPrice={oldPartialPrice}
                        partialPrice={partialPrice}
                        totalPeople={totalPeople}
                        totalDays={totalDays}
                    />
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "rootBg",
        symbol: "",
        product: "Pacote de Viagem",
        totalPrice: "1400,00",
        oldPartialPrice: "1000,00",
        partialPrice: "700,00",
        totalPeople: "2",
        totalDays: "3",
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["rootBg", "darkBg"]
        },
        symbol: { type: "string" },
        product: { type: "string" },
        totalPrice: { type: "string" },
        oldPartialPrice: { type: "string" },
        partialPrice: { type: "string" },
        totalPeople: { type: "string" },
        totalDays: { type: "string" },
    }
}

storiesOf("Components/Price", module)
    .add("Default", (args) => <PriceStory {...args}/>, argsControls);
